<template>
<div>
    <v-snackbar
          v-model="snackbar"
          top
          right
          :color="color"
          >
          {{msg}}
          <v-btn
          color="black"
          text
          @click="snackbar = false"
          >
          
          </v-btn>

          </v-snackbar>
    <v-card-title class="text-center" style="display: block;">{{title}}</v-card-title>
    <hr>
    <br>
    <v-form class="multi-col-validation" @submit.prevent="createUser"  enctype="multipart/form-data" autocomplete="off">
      <v-row>
        <v-col
          cols="12"
          md="6"
        >
          <v-text-field
            v-model="C_.Name"
            label="Name"
            
            
            dense
            hide-details
          ></v-text-field>
        </v-col>
        <v-col
          cols="12"
          md="6"
        >
          <v-text-field
            v-model="C_.Mob"
            label="Mobile"
            
            dense
            hide-details
          ></v-text-field>
        </v-col>
  
        <v-col
          cols="12"
          md="6"
        >
          <v-text-field
            v-model="C_.Username"
            label="Username"
            
            dense
            hide-details
          ></v-text-field>
        </v-col>
  
        <v-col
          cols="12"
          md="6"
         
        >
          <v-text-field
            v-model="C_.Password"
            label="Password"
            
            dense
            hide-details
          ></v-text-field>
        </v-col>
  
        <v-col
          cols="12"
          md="6"
          
          style="padding-top: 0px !important;"
        >
        <v-select
            :items="location"
            v-model="C_.Area"
            label="Choose Area"
            hide-details
          ></v-select>
        </v-col>
  
        <v-col
          cols="12"
          md="6"
          style="padding-top: 0px !important;"
        >
        <v-select
            :items="Role"
            v-model="C_.Userrole"
            label="Choose Role"
            hide-details
            required
          ></v-select>
        </v-col>
  
       
  
        
  
        <v-col cols="12">
          <v-btn color="primary" type="submit" v-if="!edited">
            Submit
          </v-btn>
          <v-btn color="primary" type="button" v-if="edited" @click="updateUser" >
            Update
          </v-btn>
          <v-btn
            type="button"
            @click="FormReset"
            class="mx-2"
          >
            Reset
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <!-- Data table -->
    <br>
    <hr>
    <v-card>
    <v-card-title>
      <span class="text-center ">Users</span>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
       item-key="id"
      :headers="headers"
      :items="Users"
      :search="search"
      
    >  
   
    
      <template v-slot:[`item.actions`]="{item}">
          <v-btn color="success" class="mx-2" fab x-small @click="editUser(item)" outlined>
            <i class="fa fa-pencil" aria-hidden="true"></i>
          </v-btn>
          <v-btn color="red" fab x-small @click="deletePost(item._id)" outlined>
            <i class="fa fa-trash" aria-hidden="true"></i>
          </v-btn>
         
        </template>
        <template v-slot:no-results>
            <v-alert :value="true" color="danger"  dark>
              Your Search {{search}} does not exist.
            </v-alert>
        </template>

</v-data-table>
  </v-card>
</div>
  </template>
  
  <script>
    import axios from "axios";
    
  export default {
    data() {
    
      return {
        snackbar:false,
        msg:'',
        color:'',
        title:'Add User',
        search:'',
        switch1:'',
        headers: [
        // {text: 'Sl No', value: 'SlNo', sortable: false, width: '150px'},
        {text: 'Name', value: 'Name', sortable: true},
        {text: 'Mobile', value: 'Mob', sortable: true},
        {text: 'Username', value: 'Username', sortable: true},
        {text: 'Service Area', value: 'Area', sortable: true},
        {text: 'User Role', value: 'Userrole', sortable: true},
        // {text: 'Status', value: 'Status', sortable: true},
        {text: 'Action', value: 'actions', sortable: true, width: '180px'},
    ],
       C_:{
          Name:'',
          Mob:'',
          Username:'',
          Password:'',
          Area:'',
          Userrole:'',
          Status:0,
  
       },
       E_id:'',
        Role:[
        "Administrator",
        "Supervisor", 
        "Service Person"
        ],
        location:[
        "Oman",
        "Kerala", 
        "UAE",
        "Suadi Arabia"
        ],
        Users:[],
        edited: false,
      }
    },
    mounted()
    {
        this.Getusers()
    },
    methods:{
        Getusers: async function(){
            let apiURL = 'api/postAPI/loadusers';
  axios.get(apiURL).then(res => {
    this.Users = res.data;
  }).catch(error => {
    console.log(error)
  });
        },
        createUser()
        {

        if(this.C_.Name != "" && this.C_.Username != "" && this.C_.Password != "" && this.C_.Mob != "" && this.C_.Area != "" && this.C_.Userrole != "")
        {
          let apiURL = 'api/postAPI/create-user';
          axios.post(apiURL, this.C_).then(res => {
          if(res.data != null)
          {
            this.Getusers()
            this.FormReset()
            console.log(res.data)
            this.color ='success'
        this.msg = ' User inserted Successfully.'
        this.snackbar = true;

           
          }else
          {
            console.log(res.data)
          }
          
          
        }).catch(error => {
          console.log(error)
        });
        }else{
        this.color ='error'
        this.msg = ' Please enter all Values.'
        this.snackbar = true;
        }
        },
        FormReset()
        {
           this.C_.Name = ''
           this.C_.Mob = ''
           this.C_.Username = ''
           this.C_.Password = ''
           this.C_.Area = ''
           this.C_.Userrole = ''
           this.edited = false;
           this.E_id = ''

        },
        editUser(item)
        {
           this.title = "Edit User : "+item.Name
           this.edited = true;
           this.C_.Name = item.Name
           this.C_.Mob = item.Mob
           this.C_.Username = item.Username
           this.C_.Password = item.Password
           this.C_.Area = item.Area
           this.C_.Userrole = item.Userrole
           this.C_.Status = 0
           this.E_id = item._id

        },
        updateUser()
        {
         console.log(this.C_)
         let apiURL = `api/postAPI/update-user/${this.E_id}`;
      axios.post(apiURL, this.C_).then((res) => {
        // console.log(res)
        if(res.status == 200)
        {
            this.title = "Add User"
            this.Getusers()
            this.FormReset()
        }else
        {
            console.log(res)
        }
        // this.close();
        // this.loadCustomer();
        this.color ='success'
        this.msg = ' Updated Successfully.'
        this.snackbar = true;
      }).catch(error => {
        console.log(error)
        console.log(this.$route.params.id)
      })
        },
        deletePost(id){
         this.$swal({
                    title: 'Are you sure? ',
                    text: "You will not be able to go back",
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#00b894',
                    cancelButtonColor: '#d63031',
                    confirmButtonText: 'Yes',
                    cancelButtonText: 'No',
                    }).then((result) => {
                        // Send request to the server
                         if (result.value) {
                               try {
                let apiURL = `api/postAPI/delete-user/${id}`;
                let indexOfArrayItem = this.Users.findIndex(i => i._id === id);
                axios.delete(apiURL).then(() => {
                        this.Users.splice(indexOfArrayItem, 1);
                    }).catch(error => {
                        console.log(error)
                    });
                //  this.$swal(
                //          'Deleted!',
                //          'User has been deleted.',
                //          'success'
                //         )
                        //Notification error
                this.color = 'error'
                this.msg = "User has been deleted."
                this.snackbar = true;
                //Notification error
            }catch (error) {
             this.$swal("Failed!", "There was something wrong."+error, "warning");
            }
                         }
                    })
  
            },
    }
  }
  </script>
  